*,*::after,*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Barlow", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.5rem;
  width: 100vw;
  height: 100vh;
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  display: none;
}

html {
  font-size: 65.2%;
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 50%;
  }
}
